.rate {
  width: 100%;
  height: 100%;
  display: flex;
  position: relative; }
  .rate__current {
    position: absolute;
    left: 0;
    top: 0;
    width: calc(100%);
    height: calc(100%);
    transform: rotate(-90deg); }
    .rate__current circle {
      transition: stroke-dashoffset 1s ease-in-out;
      transform: translate(-50%, -50%);
