      stroke-dashoffset: calc(3.14 * 2 * (50% - 2px));
      stroke-dasharray: calc(3.14 * 2 * (50% - 2px)); }
  .rate__maximal {
    width: calc(100% - 4px);
    height: calc(100% - 4px);
